<template>
  <div class="main">
    <div class="head">
      <van-field class="inputs" v-model="keyword" center placeholder="搜索DApp或输入链接进入玩耍">
        <template #left-icon>
          <div class="searchIcon">
            <img src="@/assets/search.png" alt="">
          </div>
        </template>
      </van-field>
      <div class="search" @click="search">搜索</div>
    </div>
    <div class="list">
      <div class="name">热门DAPP</div>
      <div class="dappList">
        <div class="item" v-for="(item, index) in list" @click="inDapp(item)" :key="index">
          <div class="dappIcon">
            <img :src="item.thumb" alt="">
          </div>
          <div class="dappName">{{ item.title }}</div>
        </div>
      </div>
    </div>
    <div class="list">
      <div class="slide">
        <div class="name history">历史记录</div>
        <div class="del" @click="del">
          <img src="@/assets/del.png" alt="">
        </div>
      </div>
      <div class="historyList">
        <div v-if="historyList.length == 0" class="tip">暂无历史记录</div>
        <van-cell-group v-else>
          <van-cell v-for="(item, index) in historyList" :key="index" :title="item.title" :label="item.href"
            @click="tap(item)">
            <template #icon>
              <div class="leftIcon">
                <img :src="item.thumb" alt="">
              </div>
            </template>
          </van-cell>
        </van-cell-group>
      </div>
    </div>
  </div>
</template>
<script>
import { Dialog } from 'vant';
import { callhandler } from "@/util/bridge";
// import { getDappGuide } from '@/api/request.js'
export default {
  data () {
    return {
      keyword: '',
      list: [],
      historyList: []
    }
  },
  mounted () {
    this.getDappList()
    let res = localStorage.getItem('historyList')
    if (res) {
      this.historyList = JSON.parse(res)
    }
  },
  methods: {
    async getDappList () {
      // let res = await getDappGuide()
      this.list = [
        {
          href: process.env.VUE_APP_PANCAKESWAP + '/pancakeswap/#/swap',
          thumb: require('@/assets/logo.png'),
          title: '星际'
        },
        {
          href: process.env.VUE_APP_GQC,
          thumb: require('@/assets/gqc.png'),
          title: '盲盒'
        }
      ]
    },
    tap (item) {
      this.inDapp(item)
    },
    search () {
      this.inDapp({
        href: this.keyword,
        thumb: require('@/assets/moren.png'),
        title: 'Dapp'
      })
    },
    inDapp (item) {
      let parms = {
        title: `您将跳转至第三方DApp "${item.title}"`,
        message: `您所访问的页面即将跳转至第三方DApp"${item.title}"，您在第三方DApp上的使用行为将适用该第三方DApp的用户协议`
      }
      this.dialogAlert(parms, item)
    },
    del () {
      if (this.historyList.length === 0) return
      let parms = {
        title: `提示`,
        message: `是否删除历史记录`
      }
      this.dialogAlert(parms)
    },
    dialogAlert (parms, item) {
      Dialog.confirm({
        title: parms.title,
        message: parms.message,
        confirmButtonColor: '#1F75D9',
        confirmButtonText: '我知道了'
      })
        .then(async () => {
          // on confirm
          if (item) {
            if (this.historyList.length === 0) {
              this.historyList.push(item)
            }
            let status = false
            this.historyList.forEach(element => {
              if (element.href === item.href) {
                status = true
              }
            });
            if (!status) {
              this.historyList.push({
                href: item.href,
                thumb: item.thumb,
                title: item.title
              })
            }
            localStorage.setItem('historyList', JSON.stringify(this.historyList))
            let data = {
              url: item.href
            }
            this.keyword = ''
            await callhandler("callDapp", JSON.stringify(data))
          } else {
            this.historyList = []
            localStorage.setItem('historyList', [])
          }

        })
        .catch(() => {
          // on cancel
        });
    }
  }
}
</script>
<style lang="less" scoped>
.head {
  display: flex;
  align-items: center;
  height: 50px;
  background: #fff;
  padding-left: 11px;

  .inputs {
    background: #F6F7FB;
    border-radius: 16px;
    padding: 0;
    height: 32px;

    .searchIcon {
      height: 17px;
      width: 17px;
      margin-right: 12px;
    }

    /deep/.van-field__left-icon {
      padding-left: 10px;
    }
  }

  .search {
    font-size: 14px;
    margin-left: 20px;
    margin-right: 20px;
    font-weight: 400;
    color: #333333;
    flex-shrink: 0;
  }
}

.list {
  display: flex;
  flex-direction: column;
  padding: 10px;

  .historyList {
    :deep(.van-cell) {
      align-items: center;
    }

    .leftIcon {
      height: 24px;
      width: 24px;
      border-radius: 50%;
      margin-right: 10px;
      flex-shrink: 0;
      overflow: hidden;
    }
  }

  .historyList {
    background: #FFFFFF;
    border-radius: 10px;
    overflow: hidden;
  }

  .slide {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .del {
      height: 18px;
      width: 19px;
    }
  }

  .name {
    margin: 4px 0 10px 10px;
    font-size: 14px;
    line-height: 1;
    font-weight: 400;
    color: #909CBA;
  }

  .history {
    margin-top: 8px;
  }

  .dappList {
    background: #FFFFFF;
    border-radius: 10px;
    padding: 18px 10px;
    box-sizing: border-box;
    display: grid;
    justify-items: start;
    grid-row-gap: 15px;
    justify-content: space-between;
    grid-template-columns: repeat(5, 50px);

    .item {
      display: flex;
      flex-direction: column;
      align-items: center;

      .dappIcon {
        width: 50px;
        height: 50px;
        border-radius: 10px;
        margin-bottom: 10px;
      }

      .dappName {
        font-size: 12px;
        font-size: 12px;
        transform: scale(0.917);
        font-weight: 400;
        color: #333333;
        line-height: 1;
      }
    }


  }
}

.tip {
  font-size: 14px;
  font-weight: 400;
  color: #b4b4b4;
  text-align: center;
  margin-top: 50px;
  margin-bottom: 40px;
}
</style>
